// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

* {
    box-sizing: border-box;
    &:focus {
        outline: none;
    }
}

html {
    overflow-x: hidden;
}

body {
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;

    background: linear-gradient(174.38deg, #0C3762 16.13%, #DAD9DC 64.49%);
    color: #fff;
    font-family: $ProximaNova;

    overflow-x: hidden;
}

h1 {
    font-family: $ProximaNovaTh;
    font-weight: 800;
    text-transform: uppercase;
    text-align: center;

    font-size: 55px;
    line-height: 67px;
}

header {
    background: url(../images/first-screen-img.png) no-repeat center top;
    height: 1061px;
    background-size: cover;
    padding: 15px 0;
    .logo {
        text-align: center;
    }
}

.section {
    padding: 0 50px;
}

h1.title span {
    position: relative;
    padding: 0 5px;
    &:after {
        content: '';
        width: 100%;
        height: 10px;
        background-color: #CB903B;
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;
        margin: 0 auto;
        z-index: -1;
    }
}

.section__ships p.price {
    text-align: center;
    font-size: 55px;
    line-height: 67px;
    font-family: $ProximaNovaLt;
    .icon {
        font-weight: 700;
        font-size: 45px;
    }
}

.ships__items {
    margin-top: 130px;
}

.ships__item {
    display: flex;
    margin-bottom: 140px;
    &:nth-child(even) {
        flex-direction: row-reverse;
        .ships__item__info {
            padding-left: 0;
            padding-right: 120px;
        }
        .image__wrapper {
            .img-frame {
                border-radius: 0 60px;
                left: -30px;
            }
            img {
                border-radius: 0 60px;
            }
        }
    }
}

.ships__item__image {
    width: 60%;
    .image__wrapper {
        max-width: 1065px;
        max-height: 670px;
        position: relative;
        .img-frame {
            border: 2px solid #fff;
            border-radius: 60px 0;
            width: 100%;
            height: 100%;
            position: absolute;
            top: -30px;
            left: 30px;
            z-index: 1;
        }
        img {
            max-width: 1065px;
            max-height: 670px;
            width: 100%;
            height: 100%;
            border-radius: 60px 0px;
            position: relative;
            z-index: 10;
        }
    }
    
}

.ships__item__info {
    width: 40%;
    font-size: 45px;
    line-height: 57px;
    font-weight: bold;
    padding-left: 120px;
    .grid__item__value {
        font-family: $ProximaNovaLt;
        font-size: 35px;
        line-height: 40px;
    }
    p {
        margin: 0;
    }
}

.info__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 70px;
    margin-top: 80px;
}

.info__grid__item {
    display: flex;
    align-items: center;
}
.grid__item__icon {
    min-width: 110px;
    min-height: 110px;
    border-radius: 50%;
    border: 1.5px solid #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 30px;
}

.section__callback {
    text-align: center;
    margin-bottom: 130px;
    p.title {
        font-weight: 700;
        font-size: 50px;
        line-height: 61px;
    }
    .description {
        color: #052442;
        font-size: 40px;
        line-height: 49px;
        p {
            margin: 0;
        }
        a {
            text-decoration: none;
            color: #052442;
            &:hover {
                color: #0c4986;
            }
        }
    }
}

.callback__form {
    max-width: 770px;
    margin: 0 auto;
    input {
        width: 100%;
        height: 150px;
        padding: 0 50px;
        border: none;
        margin-bottom: 55px;
        font-size: 45px;
        line-height: 55px;
        border-radius: 5px;
        font-weight: 600;
        &::placeholder {
            color: #cfcfcf;
        }
        &[type="submit"] {
            background: #0D3862;
            color: #fff;
            cursor: pointer;
            &:hover {
                background: #0c4986;
            }
        }
    }
}

footer {
    background: url(../images/footer-bg.png) no-repeat center bottom;
    background-size: cover;
    height: 800px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    color: #0D3862;
    .link {
        text-align: center;
        font-size: 30px;
        line-height: 37px;
        a {
            display: inline-block;
            font-weight: 600;
            font-size: 35px;
            line-height: 43px;
            text-decoration: none;
            margin-bottom: 20px;
        }
        .copyright {
            margin-bottom: 20px;
        }
    }
    p {
        margin: 0;
    }
    a {
        color: #0D3862;
    }
    .design-logo {
        position: absolute;
        right: 0;
        bottom: 0;
        img {
            width: 300px;
        }
    }
}

.popup-overlay {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6) repeat scroll 0 0;
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    cursor: pointer;
    z-index: 1001;
}

.popup-ty {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: #ebeef4;
    width: 691px;
    height: 150px;
    border: 5px solid #0D3862;
    -webkit-box-shadow: 0 5px 40px rgba(9, 30, 91, 0.15);
    -moz-box-shadow: 0 5px 40px rgba(9, 30, 91, 0.15);
    box-shadow: 0 5px 40px rgba(9, 30, 91, 0.15);
    padding: 25px 30px;
    z-index: 1100;
}

.wrapper-ty {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.wrapper-ty .popup-text {
    color: #111;
    font-size: 17px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
}

@media screen and (max-width: 1700px) {
    header, footer {
        height: 900px;
    }
    footer {
        background-size: cover;
    }
}

@media screen and (max-width: 1400px) {
    header, footer {
        height: 770px;
    }
    .ships__item__image {
        width: 50%;
    }
    .ships__item__info {
        width: 50%;
        font-size: 35px;
        line-height: normal;
    }
    .info__grid {
        margin-top: 30px;
        grid-row-gap: 30px;
    }
    h1.title span {
        border-bottom: 5px solid #CB903B;
        line-height: 38px;
    }
    h1.title span:after {
        height: 5px;
        bottom: 5px;
        display: none;
    }
}

@media screen and (max-width: 1200px) {
    header, footer {
        height: 660px;
        background-size: cover;
        background-position: center bottom;
    }
    .section {
        padding: 0 15px;
    }
    .ships__item {
        display: block;
    }
    .ships__item__image {
        width: 100%;
        margin-bottom: 20px;
        margin-top: 50px;
        padding: 0 45px;
        padding-left: 20px;
    }
    .ships__item:nth-child(even) .ships__item__image {
        padding-left: 45px;
    }
    .ships__item__info {
        width: 100%;
        margin-bottom: 20px;
        padding: 0 45px !important;
    }
    .image__wrapper {
        margin: 0 auto;
    }
}

@media screen and (max-width: 900px) {
    header, footer {
        height: 450px;
    }
    footer .design-logo img {
        width: 150px;
    }
}

@media screen and (max-width: 768px) {
    .ships__item__info p {
        text-align: center;
    }
    .ships__item .info__type,
    .ships__item .info__name {
        text-align: center;
        font-size: 20px;
        line-height: 24px;
        font-weight: 700;
        margin: 0;
    }
    h1 {
        font-size: 22px;
        line-height: 27px;
    }
    .section__ships p.price {
        font-size: 22px;
        line-height: 27px;
    }
    header .logo {
        text-align: right;
        padding: 10px 15px;
        img {
            max-width: 140px;
        }
    }
    header {
        height: 345px;
    }
    .section__ships p.price .icon {
        font-size: 17px;
    }
    .ships__items {
        margin-top: 0;
    }
    .ships__item {
        // margin-bottom: 0;
        // height: 100vh;
        margin: 50px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .ships__item:nth-child(even) {
        flex-direction: column;
        justify-content: center;
    }
    .grid__item__icon {
        min-width: 60px;
        min-height: 60px;
        margin-right: 10px;
        img {
            max-height: 30px;
        }
    }
    .ships__item__info .grid__item__value {
        font-size: 18px;
        line-height: 40px;
    }
    .section__callback p.title {
        font-size: 20px;
        line-height: 24px;
    }
    .callback__form {
        width: 290px;
    }
    .callback__form input {
        height: 60px;
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 20px;
    }
    .section__callback .description {
        font-size: 16px;
        line-height: 19px;
    }
    .section__callback {
        margin-bottom: 40px;
    }
    footer {
        height: 415px;
        background-size: cover;
    }
    footer .link {
        font-size: 12px;
        line-height: 15px;
    }
    footer .link a {
        font-size: 16px;
        line-height: 19px;
    }
    footer .design-logo img {
        width: 100px;
    }
}

@media screen and (max-width: 500px) {
    header {
        background-position: 100% 0;
    }
    footer {
        height: 235px;
    }
    footer .design-logo img {
        width: 70px;
    }
    .ships__item__info {
        padding: 0 15px !important;
    }
    .ships__item__image {
        padding: 0;
    }
    .ships__item:nth-child(even) .ships__item__image {
        padding-left: 0;
    }
    .ships__item__image .image__wrapper .img-frame {
        left: 0;
        top: -15px;
    }
    .ships__item:nth-child(even) .image__wrapper .img-frame {
        left: 0;
        top: -15px;
    }
}